<template>
<div class="jdbox">
  <div class="jd">
    <div class="header-top">
      <div class="header-nav">
        <i class="fa fa-navicon"></i>
      </div>
      <div class="header-title">
        <div class="header-item" :class="[item.isActive? 'titlestyle':'']" v-for="(item, index) in tabs" :key="index" v-on:click="goclick(item)">{{item.title}}</div>
      </div>
      <div class="header-plus">
        <i class="fa fa-plus"></i>
      </div>
    </div> 
    <input type="text" placeholder="搜索">
    
  </div>
  <router-view></router-view>
</div>
</template>

<script>
export default {
  name:'jd',
  data() {
    return {
      tabs:[
        {title:"推荐",isActive:true,path:{name:"musicmaster"}},
        {title:"热门歌手",isActive:false,path:{name:"mine"}},
        {title:"发现",isActive:false,path:{name:"found"}}
      ]
    }
  },
  methods:{
    goclick(item){
    if(item.isActive){
      return;
    }
    for(var i=0;i<this.tabs.length;i++){
      if(this.tabs[i].isActive){
        this.tabs[i].isActive = false;
        break;
      }
      
      // 
    }
    item.isActive = true;
    this.$router.push(item.path);
  }
  }
  
}
</script>

<style scoped lang='less'>
  .jdbox{
    width: 100%;
    .jd{
    background-color: #30c27c;
    font-size: 18px;
    font-family:"黑体";
    padding-bottom:6px;
    width: 100%;
    .header-top{
    display: flex;
    justify-content:space-between;
    
    padding: 10px 0;
    .header-nav{
      padding-left: 10px;
      color: white;
    }
    .header-title{
      .header-item{
        display: inline-block;
        padding: 0 10px;
        color: rgba(255, 255, 255, 0.8)
      }
      .titlestyle{
        color: white;
      }
    }
    .header-plus{
      padding-right: 10px;
      color: white;
    }
  }

    input{
      width: 95%;
      display: block;
       margin: 0 auto;
       height: 25px;
       background-color: rgba(0, 0, 0, 0.2);
       outline: none;
       border: none;
       color:white;
       &::-webkit-input-placeholder{
         color: white;
         text-align: center;
       }
    }
  }
  }
  
  
</style>



